<template>
  <div class="card d-flex" style="width: 200px">
    <img
      class="card-img-top mx-auto d-block"
      :src="product.image"
      alt="Card image"
      style="width: 100%"
    />

    <div class="card-body">
      <h5 class="card-title">{{ product.title }}</h5>
    </div>

    <ul class="list-group">
      <li class="d-flex flex-wrap justify-content-center">R{{ product.price }}</li>
      <li class="d-flex flex-wrap justify-content-center">{{ product.category }}</li>
      <b-badge href="#" variant="dark" @click="addToCart">Add to Cart</b-badge>
    </ul>
  </div>
</template>

<script>
export default {
  name: "ProductDetail",

  props: {
    product: Object,
  },

  methods: {
    addToCart() {
      this.$store.commit("addToCart", this.product);
    },
  },
};
</script>

<style scoped>
img {
  width: 100%;
  height: 200px;
